        "title": "Todo Bug",
        "slug": "todo-bug",
        "body": `<p>Hey! I am Mihir Panchal currently in my last year pursuing my BSc.IT degree. Well if you want to know about me drop a message in the contact section. This is my first blog post and I am confused to choose the topic, so I decided to write on recent projects I am engaged with.</p><p>&nbsp;</p><p>Lets talk about the Todo application, this is a native application (mobile devices) where the users can create teams and assign tasks to their respective teams. Well, it sounds easy but as I started working on it, it is not as simple as it seems to be and also this is my first project on native app development. Its been a month I guess starting this project, so far I have completed some modules like user authentication, creating a team, and a few more.</p><p>&nbsp;</p><p>Initially, it was going smoothly, I successfully created user authentication and it went a little tricky for me to navigate users from auth modules to the todo dashboard, however, it also went nicely. Till now I encountered hundreds of errors whether it be compilation error, runtime error, syntax error, and many silly mistakes also got introduced to some new ones, but I have a brahmastra for some bugs that seem to be impossible to solve. But I understood one thing you cannot be 100% perfect, you will encounter errors, and bugs and it has to be because, in the end, you will gain experience not in coding perfect but to solve errors, and fixing bugs.</p><p>&nbsp;</p><p>I guess I went off-topic, as I mentioned above that I completed a few modules and now struggling with a new bug. Here the bug is very simple to let me explain to you, I am logged in to the todo app and now I want to create a team, so I tapped the icon to create a team and I also successfully created it but the thing is while selecting the members from the list if I want to unselect the selected on I cannot do that, well I tried many solutions, used libraries, still working on it.</p><p>&nbsp;</p><p>So I end here to see you in another blog. Share this if you like it, and comment on your review on my first blog.</p><p><br></p>`,
