        "title": "BUG SOLVED! YET MORE COMING",
        "slug": "BUG-SOLVED!-YET-MORE-COMING",
        "body": `<p>Today I have good news to share with you, remember the bug I posted in my first blog, well it has been a while as I was busy with some college stuff. So I was about to say good news that I solved the bug.</p><p><br></p><p>The solution to that was very simple, first, let me explain the bug once again. While creating a team user needed to select team members from the list of the users, and I was mapping the users using the FlatList component that is provided with react-native, so users can select the users but the issue was that the user cannot unselect the team-member once selected, this would be very annoying and would put bad impression for the app. So I went through various sites, and documentation and tried many more other possibilities, but didnt work. Again next morning repeating the same process, while I was going through some documentation there I got triggered to search for libraries that do some stuff like multi-selecting elements. And there was my need for react-native-multi-select this is the react-native library that maps the elements and enables to select and unselect items. Installed it and gave it a try, hoping it works fine, and this it worked flawlessly, and was able to complete the team-create module, also this package helped me with selecting a single user to assign a task. Thanks to the react-native developer's community for making really awesome and useful libraries.</p><p><br></p><p>Now the Todo-app is in a good state and progressing well. I added some more features to it like team members could discuss the assigned tasks with the team leader, its a kind of one-to-one chat. Yet there are some more changes to make like the discussion needs to only be visible to assigned-member and the team leader, other team members should not be able to read this discussion. Also, I want to add push notifications in this app, but I wont be adding it in the beta release, well I tried to add a module to upload pdfs but it seems to be very buggy, or I am unable to integrate it efficiently. I would be releasing the beta v1 soon as I guess this build needs to be tested in the production mode and based on the responses I will improve the stability and efficiency of this app, which will also include push notifications and pdf upload in further beta versions.</p><p><br></p><p>Well, I guess it was getting more official, so I end here to see you in another blog. Share it if you like and comment on your review.</p>`,
