        "title": "TODO 2.0!!!! Not Complete Yet",
        "slug": "TODO-2.0!!!!-Not-Complete-Yet",
        "body": `<p>Finally, the first beta of TODO 2.0 is rolling out, you can check out the website todo-20.netlify.app for more details and download it, well initially I was thinking to announce in my official website but later I changed my mind as I will be announcing the app once the stable beta is released, so hold on a little more soo n stable beta will be released.</p><p><br></p><p> </p><p>As of now, this beta is under testing, so you might be thinking that I hired a team just to test my application well that is true. These testers are quite professional and punctual, they are testing this application very precisely, recognizing every single mistake of and god knows what. I n return I will be giving them a treat, yes just a treat they dont deserve more than that, the famous team is none other than my friends.</p><p><br></p><p> </p><p>So the beta is released now let's talk about the further changes I will be adding, I guess the major changes will be for UI in authentication flow to be specific. I will be adding in put of email on the splash screen, the user will enter their email-id and the back-end will search for the user based on that email, if the user exists then it will be directed to the login page where he will be asked for a password, if the user doesnt exist then it will be directed to register screen to get itself registered. Further UI changes will be for form elements, for now, I am using react-native-elements for basic styled UI components, but for forms, I will switch to react-native-paper. Need dozen of improvements for the Create Todo screen, as for now, it is a bit confusing for the end-user to figure out. I Will also include group discussion in the team, well this is going to take time as first I will implement socket.io for a chat. And one more thing I found a bug that the team members are able to see the chats between the individual team member and team leader, so need to fix this before socket.io.</p><p><br></p><p> </p><p>OK, so far these were few UI changes, now coming to major changes, implementing this will change the working of the application. Let me explain this first, now the list on the Create Todo screen are all the users registered in the app as far as testing and small-scale use this may work but once it is globally available there will be n numbers of users so to tackle this situation user will register in this application along with their mobile number. This would work similarly to What's App, users will be listed the list of the user that has registered and exist in their contacts. This would help the user to create the team as they dont need to go through the whole list every time.</p><p><br></p><p> </p><p>Hope you like the blog, share your comments below. See you soon in my next blog.</p><p><br></p>`,
        "excerpt" :"Finally, the first beta of TODO 2.0 is rolling out, you can check out the website todo-20.netlify.app for more details ...",
        "mtitle": "This is M-Title",
        "mdesc": "This is M-desc",
